///-
import unit from '../unit/index.js'
import Block from '../unit/block.js'

const mutations={
//	
	nextBlock(state, data){
    	if (!data) {
      		data = unit.getNextType();
    	}
    	state.next = data;
  	},
//	
  	matrix(state, data){
    	state.matrix = data;
  	},
//	
	moveBlock(state, data){
    	state.cur = data.reset === true ? null : new Block(data);
  	},
	reset(state, data){
    	state.reset = data;
  	},
//	/
  	lock(state, data){
    	state.lock = data;
  	},
//	/
  	drop(state, data){
    	state.drop = data;
  	},
//	
  	pause(state, data){
    	state.pause = data;
  	},
//	
	clearLines(state, data){
    	state.clearLines = data;
	},
//	
	music(state, data){
    	state.music = data;
  	},
//	
	speedRun(state, data){
    	state.speedRun = data;
	},
//	
	speedStart(state, data) {
    	state.speedStart = data;
  	},
//	
	points(state, data){
    	state.points = data;
  	},
//	
  	maxPoints(state, data){
  		console.log()
    	state.maxPoints = data;
  	},
//	
  	startLines(state, data){
    	state.startLines = data;
  	},
}

export default mutations


